import React, { useState } from 'react';
import Project from '../../Components/Project/Project';
import Container from 'react-bootstrap/Container';
import Grid from '@material-ui/core/Grid';
import useMediaQuery from '@material-ui/core/useMediaQuery';

//images
import css_logo from './css_logo.png';
import html_logo from './html_logo.png';

import react_logo from './react_logo.png';
import material_ui_logo from './material_ui_logo.png';

import dota_stats from './dota_stats.png';
import aluminus from './aluminus.png';

const Projects = [
    {
        name: 'OpenDota API Stats',
        img: dota_stats,
        site: 'https://burself21.github.io/dota-stats',
        source: 'https://github.com/burself21/dota-stats',
        desc: 'The website displays organized and detailed information about all of the various heroes and items in the game Dota 2 by accessing the Opendota API',
        techs: [
            {
                name: "HTML",
                icon: html_logo
            },

            {
                name: "CSS",
                icon: css_logo
            },

            {
                name: "React",
                icon: react_logo
            },
            {
                name: "Material UI",
                icon: material_ui_logo
            }
        ]
    },

    {
        name: 'Aluminus (In Progress)',
        img: aluminus,
        site: 'https://burself21.github.io/aluminus',
        source: 'https://github.com/burself21/aluminus',
        desc: 'The website displays organized and detailed information about all of the various heroes and items in the game Dota 2 by accessing the Opendota API',
        techs: [
            {
                name: "HTML",
                icon: html_logo
            },

            {
                name: "CSS",
                icon: css_logo
            },

            {
                name: "React",
                icon: react_logo
            },
            {
                name: "Material UI",
                icon: material_ui_logo
            }
        ]
    }
]

const ProjectContainer = props => {
    
    const header = <h1 style={{marginBottom: "40px", textAlign: "center"}}> Projects </h1>;
    const num_projects = Projects.length;

    if (num_projects === 0) {
        return (
            <Container className='p-3 project-container-main' id="project-container">
                {header} <br />
                <h5> Sorry. At this moment, Nathan does not have any notable projects :( </h5>
            </Container>
        )
    }
    else {
        return (
            <Container className='p-3 project-container-main' id="project-container">
                {header}
                <Grid container spacing={6}>
                    {/** change Array.slice(0) to Array */}
                    {Array.from(Array(num_projects).keys()).map(id => 
                        <Grid item key={id} xs={12} sm={6}>
                                <Project
                                    {...Projects[id]}
                                />
                        </Grid> 
                    )}
                </Grid>
            </Container>
        )
    }
}

export default ProjectContainer;