import React from 'react';

import styles from './Intro.module.css';

import ScrollButton from '../ScrollButton/ScrollButton';

import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';

import gh_logo from './gh_logo.png';
import linkedin_logo from './linkedin_logo.png';

const useStyles = makeStyles(theme => ({
    header: {
        color: "white",
        fontFamily: "'Roboto', sans-serif",
        fontWeight: 500
    }
}));

const Intro = () => {

    const classes = useStyles();

    return (
        <div id={styles.container}>
            <div id={styles.mask}>
            </div>
            <div id={styles.overlay}>
                <Typography variant="h2" className={classes.header}>
                    Hi, I'm Nathan Johns
                </Typography>
                <div id={styles.icons}>
                    <IconButton href="https://github.com/burself21">
                        <img src={gh_logo} alt="" height="64" width="64"/>
                    </IconButton>
                    <IconButton href="https://www.linkedin.com/in/nathan-johns-6b421b162">
                        <img src={linkedin_logo} alt="" height="64" width="64"/>
                    </IconButton>
                </div>
                <ScrollButton text="Take a look at my projects" />
                <div style={{height: "60px", zIndex: "-1000"}}> 
                </div> 
            </div>
        </div>
    )
}

export default Intro;